.buttonSet {
  height: auto;
  background-color: black;
  margin: 10px auto;
  padding: 20px;
  border-radius: 10px;
}

#button {
  width: 100%;
  color: white;
  background-color: black;
  border: none;
  font-weight: 600;
}

@media (min-width: 600px) {
  #button {
    font-size: 24px;
  }
}